import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

class ExamResult extends Component {
    constructor(props) {
        super(props);
        this.state = {
            listResults : []
        }
    }

    componentWillMount() {
        const _id = this.props.match.params.id;
        axios.get('/api/examResults/'+_id)
        .then((res)=>{
            this.setState({
                listResults: res.data
            });
        })
        .catch((err)=>{

        });
        
    }
    printResult = ()=>{
        return this.state.listResults.map((value, key)=>{
            return (
                <div className="card-body" key={key}>
                    <h4 className="card-title">Chúc mừng bạn đã hoàn thành bài thi!</h4>
                    <div className="card-text">
                        <h5>Kết quả</h5>
                        <h5>{"Số câu trả lời đúng: " + value.number_correct + "/" + value.total_complete}</h5>  
                    </div>
                    <Link to={"xem-ket-qua."+value._id} className="btn btn-info">Xem Kết quả</Link>
                </div>                                       
             );
        });
    }
    render() {
        return (
           <div className="container-fluid" style={{height:640, backgroundColor:'#F0F0F0'}}>
               <div className="container">
                    <div className="row">
                        <div className="m-auto" style={{position:"relative",top:100}}>
                            <div className="card text-center" style={{width: '38rem'}}>
                                {this.printResult()}
                            </div>
                        </div>
                    </div>
                </div>
           </div>
        );
    }
}

export default ExamResult;