import React, { Component } from 'react';
import collection from 'lodash/collection';

class Question extends Component {
    constructor(props) {
        super(props);
        this.state = {
            answers:[]
        };
    }

    isChange = (event)=>{
        const name = event.target.name;
        const value = event.target.value;

        this.setState({
            [name]:value,
        });

        this.props.getQuestionData({id:name, value: value});
    }

    componentWillMount() {
        const tmpArr = [...collection.shuffle(this.props.answers)];
        this.setState({
            answers: tmpArr
        });
    }
    

    printAnswer = ()=>{
        const arrStr = ['A.','B.','C.','D.'];
        let indexArr = -1; 
        return this.state.answers.map((value, key)=>{
            indexArr++;
            return (<li key={key}>
                <div className="form-check">
                    {/* <input type="radio" onChange={(event)=>{this.isChange(event)}} className="form-check-input" name={this.props.id} defaultValue={value.answer_value} />            */}
                    <label className="custom-control material-checkbox">
                        <input type="radio" className="material-control-input" onChange={(event)=>{this.isChange(event)}} name={this.props.id} defaultValue={value.answer_value} />
                        <span className="material-control-indicator" style={{top: 2}}/>
                        &nbsp;
                        {arrStr[indexArr] + value.answer_name}
                    </label>
                    {/* <label>
                        {arrStr[indexArr] + value.answer_name}
                    </label> */}
                </div>
            </li>);
        });
    }
      
    render() {
        return (
            <div className={"carousel-item "+ this.props.active}>
                <ul style={{ listStyle: 'none' }}>
                    <li>
                        <span style={{fontSize: "1.2em", fontWeight: "bold"}}>{"Câu "+ this.props.number + ": "+ this.props.question_name}</span>
                    </li>
                        {this.printAnswer()}
                </ul>
            </div>
        );
    }
}

export default Question;