import React, { Component } from 'react';
import { Link } from "react-router-dom";
import axios from 'axios';

const arrColor = ['bg-primary','bg-success','bg-info','bg-secondary','bg-warning'];
const NUMBER_LIMIT_PAGE = 60;

const toSlugUrl = (str) => {
    // Chuyển hết sang chữ thường
    str = str.toLowerCase();     
 
    // xóa dấu
    str = str.replace(/(à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ)/g, 'a');
    str = str.replace(/(è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ)/g, 'e');
    str = str.replace(/(ì|í|ị|ỉ|ĩ)/g, 'i');
    str = str.replace(/(ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ)/g, 'o');
    str = str.replace(/(ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ)/g, 'u');
    str = str.replace(/(ỳ|ý|ỵ|ỷ|ỹ)/g, 'y');
    str = str.replace(/(đ)/g, 'd');
 
    // Xóa ký tự đặc biệt
    str = str.replace(/([^0-9a-z-\s])/g, '');
 
    // Xóa khoảng trắng thay bằng ký tự -
    str = str.replace(/(\s+)/g, '-');
 
    // xóa phần dự - ở đầu
    str = str.replace(/^-+/g, '');
 
    // xóa phần dư - ở cuối
    str = str.replace(/-+$/g, '');
 
    // return
    return str;
}

class Subject extends Component {
    constructor(props) {
        super(props);
        this.state = {
            bgColor: "",
            pageLimit: 0,
            pageTotal: 0,
            styleActiveLink:{
                pointerEvents: "none", 
                cursor:"default"
            }
        }
    }

    componentWillMount() {
        var _color = arrColor[(Math.random() * arrColor.length) | 0]
        this.setState({
            bgColor: _color
        });

        const id = this.props._id;
        axios.get('/api/questions/getQuestionById/'+id)
        .then((res)=>{
            const _pageTotal = Math.ceil(res.data.length / NUMBER_LIMIT_PAGE);
            console.log(res.data.length / NUMBER_LIMIT_PAGE, _pageTotal);
            
            this.setState({
                pageTotal: _pageTotal
            });
        })
        .catch((err)=>{

        });
    }

    isChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        let _style = {
            pointerEvents: "none", 
            cursor:"default"
        }

        if (value) {
            _style = {
                pointerEvents: "auto", 
                cursor:"pointer"
            }
        }

        this.setState({
            [name]:value,
            styleActiveLink: _style
        });
    }

    printSelectSubject = ()=>{
        var _options = [];
        for (let _i = 0; _i < this.state.pageTotal; _i++) {
            _options.push(<option key={_i} value={_i}>{_i + 1}</option>);
        }
        
        return _options;
    }
    
    render() {
        return (
            <div className="col-md-4">
                <div className={"card text-white " + this.state.bgColor +" flex-md-row mb-4 shadow-sm h-md-250"} style={{width: 370}}> 
                    <img className="card-img-right flex-auto d-none d-lg-block" alt={this.props.subject_name} src={this.props.src_img} style={{width: 200, height: 200}} />
                    <div className="card-body d-flex flex-column align-items-start justify-content-start">
                        <strong className="d-inline-block mb-2 text-white">{this.props.subject_name}</strong>
                        <div className="d-inline-block mb-2 text-white">
                            <span>Chọn đề</span>
                            <select name="pageLimit" onChange={(event)=>{this.isChange(event)}} required>
                                <option value="">--Chọn đề thi--</option>
                                {this.printSelectSubject()}
                            </select>
                        </div>
                        <Link to={'/bai-thi-mon-'+toSlugUrl(this.props.subject_name)+'.'+this.props._id + '/'+this.state.pageLimit} className="btn btn-outline-light btn-sm" style={this.state.styleActiveLink} role="button">Vào thi</Link>
                    </div>
                </div>
            </div>
        );
    }
}

export default Subject;