import React, { Component } from 'react';
import Question from './Question';
import axios from 'axios';

class CardQueston extends Component {

    constructor(props) {
        super(props);
        this.state = {
            dataResult: [],
            questions:[],
            numberQuetion:  1
        }
    }

    componentWillMount() {
        const id = this.props.match.params.id;
        const pageLimit = this.props.match.params.pageLimit;
        
        axios.get('/api/questions/pagenation/'+id+'/'+pageLimit)
        .then((res)=>{
            this.setState({
                questions: res.data
            });
        })
        .catch((err)=>{

        });
    }  
    
    getQuestionData = (data) => {
        let indexOfResult = this.state.dataResult.findIndex(i => i.id === data.id);
        
        if (indexOfResult === -1) {
            this.setState({
                dataResult: [...this.state.dataResult, data]
            });
        } else {
            let tempArr = this.state.dataResult;
            tempArr[indexOfResult] = data;
            this.setState({
                dataResult: tempArr
            });
        }              
    }

    printQuestion = () => {
        let numberQuetion = 0;
        return this.state.questions.map((value, key)=>{
            numberQuetion++;
            return (<Question active={numberQuetion===1?"active":""}
            key={key}
            number={numberQuetion}
            id={value._id}
            question_name={value.question_name}
            answers={value.answers}
            getQuestionData={(data)=>{this.getQuestionData(data)}}/>);
        });
    }

    nextQuestion = ()=>{
        if (this.state.numberQuetion >= this.state.questions.length) {
            this.setState({
                numberQuetion: 1
            });
        } else {
            this.setState({
                numberQuetion: this.state.numberQuetion + 1
            });
        }
    }

    preQuestion = ()=>{
        this.setState({
            numberQuetion: this.state.numberQuetion - 1
        });
    }

    submitQuestion = ()=>{
        let tmpQuestions = [...this.state.questions];
        const dataResult = [...this.state.dataResult];
        const arrResultCorrect = [...tmpQuestions.filter(o => dataResult.find(o2 => o._id === o2.id && o.answer_correct === o2.value))];

        tmpQuestions = tmpQuestions.map((obj1)=>{
            const tmpObj = {...obj1}
            dataResult.forEach((obj2)=>{
                if (tmpObj._id === obj2.id) {
                    tmpObj.answer_chooise = obj2.value;
                }
            });
            return tmpObj;
        });
        
        // alert("Câu trả lời đúng của bạn là : "+ arrResultCorrect.length+"/"+this.state.questions.length);

        const _result = {
            exam_code : this.props.match.params.id,
            username: localStorage.getItem('user'),
            number_correct : arrResultCorrect.length,
            total_complete : this.state.questions.length
        }
        axios.post('/api/examResults/add',_result).then((res)=>{
            if(res.status === 200){
                
                const questionHistory = {
                    exam_result_id: res.data._id,
                    questions: tmpQuestions
                };
                console.log(questionHistory);
                
                axios.post('/api/questionHistorys/add',questionHistory).then((res2)=>{
                    if(res2.status === 200){
                        console.log("insert success");
                    }
                }).catch((err)=>{
                    console.log(err);
                });

                this.props.history.push('/ket-qua-thi.'+res.data._id,);
            }
        }).catch((err)=>{
            console.log(err);
        });       
    }

    activePreButton = ()=>{
        if (this.state.numberQuetion === 1) {
            return (<a className="btn btn-primary" onClick={()=>{this.preQuestion()}} href={"#carouselId"} data-slide="prev" style={{marginRight: "16px", pointerEvents: "none", cursor:"default"}} disabled>
                        <span>Trở về</span>
                    </a>);
        } 
        return (<a className="btn btn-primary" onClick={()=>{this.preQuestion()}} href={"#carouselId"} data-slide="prev" style={{marginRight: "16px"}}>
                    <span>Trở về</span>
                </a>);
    }

    render() {
        if(this.state.questions.length === 0){
            return null;
        }
        return (
            <div className="container-fluid" style={{backgroundColor:'#F0F0F0', height: 640}}>
                <div className="container">
                    <div className="row">
                        <div className="col-8 m-auto" id="card-question">
                            <div className="card">
                                <div className="card-title text-center">
                                    <h4 className="card-title">{"Câu hỏi "+this.state.numberQuetion + "/"+this.state.questions.length}</h4>
                                </div>
                                <div className="card-body">
                                    <div className="card-text">
                                        <div className="row">
                                            <div className="col">
                                                <div id="carouselId" className="carousel slide" data-ride="carousel" data-interval="false">
                                                    <div className="carousel-inner" role="listbox">
                                                        {this.printQuestion()}
                                                    </div>
                                                    <div className="row">
                                                    <div className="m-auto">
                                                        {this.activePreButton()}
                                                        <a className="btn btn-primary" onClick={()=>{this.nextQuestion()}} href={"#carouselId"} data-slide="next">
                                                            <span>Tiếp theo</span>
                                                        </a>
                                                    </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row mt-2">
                        <button type="submit" onClick={()=>{this.submitQuestion()}} className="btn btn-primary m-auto">Nộp bài thi</button>
                    </div>
                </div>
            </div>

        );
    }
}

export default CardQueston;