import React, { Component } from 'react';
import axios from 'axios';

const arrStr = ['A.','B.','C.','D.'];
var answerCorrect = ''
class ViewResult extends Component {
    constructor(props) {
        super(props);
        this.state = {
            examResults: [],
            questions:[],
            answerCorrect: ''
        }
    }

    componentWillMount() {
        const id = this.props.match.params.id;
        
        axios.get('/api/questionHistorys/'+id)
        .then((res)=>{
            if (res.data) {
                this.setState({
                    examResults: res.data,
                    questions:res.data[0].questions
                });
            }
        })
        .catch((err)=>{

        });
    } 
    
    printAnswer = (question)=>{
        let indexArr = -1;
        let indexOfResult = question.answers.findIndex(i => i.answer_value === question.answer_correct);

            answerCorrect = arrStr[indexOfResult] + question.answers[indexOfResult].answer_name

        return question.answers.map((value, key) => {
            indexArr++;
            if (question.answer_chooise === question.answer_correct 
                    && question.answer_correct === value.answer_value) {
                return (<li key={key}>
                    <div className="form-check" style={{fontWeight:"bold",fontStyle: 'italic'}}>
                        <input type="radio" readOnly style={{pointerEvents:"none"}}  className="form-check-input" name={question._id} defaultValue={value.answer_value} checked/>
                        <span>
                            {arrStr[indexArr] + value.answer_name}
                        </span>
                        <i className="fas fa-check fa-lg fa-fw" style={{color: "#28a745"}}/>
                    </div>
                </li>);
            } else if(question.answer_chooise === value.answer_value) {      
                return (<li key={key}>
                    <div className="form-check" style={{fontWeight:"bold",fontStyle: 'italic'}}>
                        <input type="radio" readOnly style={{pointerEvents:"none"}}  className="form-check-input" name={question._id} defaultValue={value.answer_value} checked/>
                        <span>
                            {arrStr[indexArr] + value.answer_name}
                        </span>
                        <i className="fas fa-times fa-lg fa-fw" style={{color: "red"}}/>
                    </div>
                </li>);
            }
            return (<li key={key}>
                <div className="form-check">
                    <input type="radio" readOnly style={{pointerEvents:"none"}} className="form-check-input" name={question._id} defaultValue={value.answer_value} />
                    <span>
                        {arrStr[indexArr] + value.answer_name}
                    </span>
                </div>
            </li>);
        });
    }

    printQuestions = () => {
        let numberQuetion = 0;
        return this.state.questions.map((_value, key)=>{
            numberQuetion++;
            return (
                <div className="row"  key={key}>
                    <div>
                        <ul style={{ listStyle: 'none' }}>
                            <li>
                                <span style={{fontSize: "1.2em", fontWeight: "bold"}}>{"Câu "+ numberQuetion + ": "+ _value.question_name}</span>
                            </li>
                            {this.printAnswer(_value)}
                            <li>
                                <span style={{fontSize: "1.1em", fontWeight: "bold"}}>{"Đáp án: "}</span>
                                <span style={{fontStyle: 'italic'}}>{answerCorrect }</span>
                            </li>
                        </ul>
                    </div>
                </div>
            );
        }); 
    }

    render() {
        console.log(this.state.questions);
        
        return (
            <div className="container" style={{marginTop: 90}}>
                <div className="row text-center">
                    <div className="col" style={{fontSize: '2.2em'}}>Kết quả thi</div>
                </div>
               {this.printQuestions()}
            </div>
        );
    }
}

export default ViewResult;