import React, { Component } from 'react';
import { Link } from "react-router-dom";

class CardInfo extends Component {
    render() {
        return (
            <div className="col-sm-4 mb-2">
                <div className="card w-70 text-center">
                    <div className="card-body">
                    <h5 className="card-title">{'Lần thi thứ '+this.props.number}</h5>
                    <div className="card-text">
                        <label>{'Số câu trả lời đúng : ' + this.props.number_correct}</label>
                        <label>{'Số câu đã hoàn thành : ' + this.props.total_complete}</label>
                    </div>
                    <Link to={'/xem-ket-qua.'+this.props._id} className="btn btn-primary">Xem kết quả</Link>
                    </div>
                </div>
            </div>

        );
    }
}

export default CardInfo;