import React, { Component } from 'react';
import axios from 'axios';
import CardInfo from './CardInfo';

class ViewInfo extends Component {
    constructor(props) {
        super(props);
        this.state = {
            listSubject:[],
            listResult:[]
        }
    }
    componentWillMount() {
        axios.get('/api/subjects')
        .then((res)=>{
            console.log(res.data);
            this.setState({
                listSubject: res.data
            });
        })
        .catch((err)=>{

        });
    }

    isChange = (event)=>{
        // const name = event.target.name;
        const value = event.target.value;
        const usernamId = this.props.match.params.id;
        console.log(value, usernamId);
        
        axios.get('/api/examResults/'+usernamId+'/'+value)
        .then((res)=>{
            if (res.status === 200) { 
                console.log(res.data);
                this.setState({
                    listResult: res.data
                });
            }
        })
        .catch((err)=>{

        });
    }
    printSelectSubject = ()=>{
        return this.state.listSubject.map((value, key)=>{
            return (<option key={key} value={value._id}>{value.subject_name}</option>);
        });
    }
    printResultInfo = ()=>{
        let numberIncre = 0;
        return this.state.listResult.map((value, key)=>{
            numberIncre++;
            return (
                <CardInfo
                    key={key}
                    _id = {value._id}
                    number = {numberIncre}
                    number_correct = {value.number_correct}
                    total_complete = {value.total_complete}
                />
            )
        });
    }
    render() {
        return (
            <div className="container" style={{marginTop: 90}}>
                <div className="row">
                        <div className="card m-auto">
                            <div className="card-header">
                                Chọn thông tin cần xem
                            </div>
                            <div className="card-body">
                                <div className="input-group">
                                    <select className="form-control" name="exam_code" onChange={(event)=>{this.isChange(event)}} required>
                                        <option value="">--Chọn môn thi--</option>
                                        {this.printSelectSubject()}
                                    </select>
                                </div>
                            </div>
                    </div>
                </div>
                <div className="row mt-5">
                    {this.printResultInfo()}
                </div>
            </div>
        );
    }
}

export default ViewInfo;