import React, { Component } from 'react';
import { Route } from "react-router-dom";
import Login from '../components/Login';
import CardQueston from './../components/CardQueston';
import ListSubject from '../components/ListSubject';
import WithAuth from './WithAuth';
import AddQuestion from '../components/admin/AddQuestion';
import ExamResult from '../components/ExamResult';
import ViewResult from '../components/ViewResult';
import ViewInfo from './../components/ViewInfo';

class Routers extends Component {
    render() {
        return (
            <React.Fragment>
                <Route exact path="/" component={Login}/>
                <Route path="/danh-sach-mon-thi" component={WithAuth(ListSubject)}/>
                <Route path="/bai-thi-mon-:slug.:id/:pageLimit" component={WithAuth(CardQueston)}/>
                <Route path="/admin/question/add" component={WithAuth(AddQuestion)}/>
                <Route path="/ket-qua-thi.:id" component={WithAuth(ExamResult)}/>
                <Route path="/xem-ket-qua.:id" component={WithAuth(ViewResult)}/>
                <Route path="/xem-thong-tin.:id" component={WithAuth(ViewInfo)}/>
            </React.Fragment>
        );
    }
}

export default Routers;