import React, { Component } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';
import axios from 'axios';
import lang from 'lodash/lang';

class AddQuestion extends Component {
    constructor(props) {
        super(props);
        this.state = {
            listSubject : [],
            question_name:"",
            answer_1:"",
            answer_2:"",
            answer_3:"",
            answer_4:"",
            answer_correct:"",
            exam_code:"",
        }
    }

    componentWillMount() {
        axios.get('/api/subjects')
        .then((res)=>{
            console.log(res.data);
            this.setState({
                listSubject: res.data
            });
        })
        .catch((err)=>{

        });
    }

    printSelectSubject = ()=>{
        return this.state.listSubject.map((value, key)=>{
            return (<option key={key} value={value._id}>{value.subject_name}</option>);
        });
    }

    isChange = (event)=>{
        const name = event.target.name;
        const value = event.target.value;
        console.log(name, value);
        
        this.setState({
            [name]:value
        });
    }

    notifyAddData = () => {
        toast.success(`Thêm record thành công!`, {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true
        });
    }

    notifyCheckValidator = (_message) => {
        toast.warn(_message, {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true
        });
    }

    notifyError = (_message) => {
        toast.error(_message, {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true
        });
    }

    onSubmit = (event)=>{
        event.preventDefault();
        if (lang.isEmpty(this.state.question_name)) {
            this.notifyCheckValidator('Vui lòng nhập câu hỏi!');
            return;
        }
        if (lang.isEmpty(this.state.answer_1) || lang.isEmpty(this.state.answer_2) 
            || lang.isEmpty(this.state.answer_3) || lang.isEmpty(this.state.answer_4)) {
            this.notifyCheckValidator('Vui lòng nhập câu trả lời!');
            return;
        }
        
        if (lang.isEmpty(this.state.answer_correct)) {
            this.notifyCheckValidator('Vui lòng chọn đáp án cho câu hỏi!');
            return;
        }
        if (lang.isEmpty(this.state.exam_code)) {
            this.notifyCheckValidator('Vui lòng nhập môn thi!');
            return;
        }
        const _answers = [
            {
                answer_name: this.state.answer_1,
                answer_value: "answer_1"
            },
            {
                answer_name: this.state.answer_2,
                answer_value: "answer_2"
            },
            {
                answer_name: this.state.answer_3,
                answer_value: "answer_3"
            },
            {
                answer_name: this.state.answer_4,
                answer_value: "answer_4"
            }
        ];
        console.log(_answers);
        
        const _question = {
            question_name: this.state.question_name,
            answers: _answers,
            answer_correct: this.state.answer_correct,
            exam_code: this.state.exam_code
        }
        console.log(_question);
        axios.post('/api/questions/add',_question)
        .then((res)=>{
            if (res.status === 200) {
                console.log("Thêm thành công!");
                this.setState({
                    question_name:"",
                    answer_1:"",
                    answer_2:"",
                    answer_3:"",
                    answer_4:"",
                    answer_correct:"",
                    exam_code:""
                });
            
                this.notifyAddData();

                document.getElementById('btn_reset').click();
            }
        }).catch((err)=>{
            console.log(err);
            this.notifyError('Thêm câu hỏi thất bại!');
        });
    }
    
    render() {
        return (
            <div className="container">
                <div className="row" style={{marginTop:80}}>
                    <div className="col-10 m-auto">
                    <form>
                        <div className="form-group row">
                        <label htmlFor="colFormLabel" className="col-sm-2 col-form-label">Câu hỏi</label>
                        <div className="col-sm-8">
                            <div className="input-group">
                            <input type="text" onChange={(event)=>{this.isChange(event)}} name="question_name" defaultValue={this.state.question_name} className="form-control" required />
                            </div>
                        </div>
                        </div>
                        <div className="custom">
                        <div className="form-group row">
                            <label htmlFor="colFormLabel" className="col-sm-2 col-form-label">Đáp án</label>
                            <div className="col-sm-8">
                            <div className="row" style={{alignItems: 'center',justifyContent: 'center'}}>
                                <div className="col-sm-11">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                    <div className="input-group-text">A</div>
                                    </div>
                                    <input type="text" onChange={(event)=>{this.isChange(event)}} name="answer_1" defaultValue={this.state.answer_1} className="form-control" required />
                                </div>
                                </div>
                                <div className="col-sm-1">
                                <div className="form-check form-check-inline">
                                    <label className="custom-control material-checkbox">
                                        <input type="radio" className="material-control-input" onChange={(event)=>{this.isChange(event)}} name="answer_correct" defaultValue="answer_1" />
                                        <span className="material-control-indicator" />
                                    </label>
                                </div>
                                </div>
                            </div>
                            </div>
                        </div>
                        <div className="form-group row">
                            <div className="col-sm-2" />
                            <div className="col-sm-8">
                            <div className="row">
                                <div className="col-sm-11">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                    <div className="input-group-text">B</div>
                                    </div>
                                    <input type="text" onChange={(event)=>{this.isChange(event)}} name="answer_2" defaultValue={this.state.answer_2} className="form-control" required />
                                </div>
                                </div>
                                <div className="col-sm-1">
                                <div className="form-check form-check-inline">
                                    <label className="custom-control material-checkbox">
                                        <input type="radio" className="material-control-input" onChange={(event)=>{this.isChange(event)}} name="answer_correct" defaultValue="answer_2" />
                                        <span className="material-control-indicator" />
                                    </label>
                                </div>
                                </div>
                            </div>
                            </div>
                        </div>
                        <div className="form-group row">
                            <div className="col-sm-2" />
                            <div className="col-sm-8">
                            <div className="row">
                                <div className="col-sm-11">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                    <div className="input-group-text">C</div>
                                    </div>
                                    <input type="text" onChange={(event)=>{this.isChange(event)}} name="answer_3" defaultValue={this.state.answer_3} className="form-control" required />
                                </div>
                                </div>
                                <div className="col-sm-1">
                                <div className="form-check form-check-inline">
                                    <label className="custom-control material-checkbox">
                                        <input type="radio" className="material-control-input" onChange={(event)=>{this.isChange(event)}} name="answer_correct" defaultValue="answer_3" />
                                        <span className="material-control-indicator" />
                                    </label>
                                </div>
                                </div>
                            </div>
                            </div>
                        </div>
                        <div className="form-group row">
                            <div className="col-sm-2" />
                            <div className="col-sm-8">
                            <div className="row">
                                <div className="col-sm-11">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                    <div className="input-group-text">D</div>
                                    </div>
                                    <input type="text" onChange={(event)=>{this.isChange(event)}} name="answer_4" defaultValue={this.state.answer_4} className="form-control" required />
                                </div>
                                </div>
                                <div className="col-sm-1">
                                <div className="form-check form-check-inline">
                                    <label className="custom-control material-checkbox">
                                        <input type="radio" className="material-control-input" onChange={(event)=>{this.isChange(event)}} name="answer_correct" defaultValue="answer_4" />
                                        <span className="material-control-indicator" />
                                    </label>
                                </div>
                                </div>
                            </div>
                            </div>
                        </div>
                        <div className="form-group row">
                            <label htmlFor="colFormLabel" className="col-sm-2 col-form-label">Môn thi</label>
                            <div className="col-sm-4">
                            <div className="input-group">
                                <select className="form-control" name="exam_code" onChange={(event)=>{this.isChange(event)}} required>
                                    <option value="">--Chọn môn thi--</option>
                                    {this.printSelectSubject()}
                                </select>
                            </div>
                            </div>
                        </div>
                        <div className="form-group row">
                            <div className="col-sm-2" />
                            <div className="col-sm-8">
                            <div className="row">
                                <div className="col-sm-6">
                                <button type="submit" onClick={(event)=>{this.onSubmit(event)}} className="btn btn-primary btn-block">Thêm</button>
                                </div>
                                <div className="col-sm-6">
                                <button type="reset" id='btn_reset' className="btn btn-dark btn-block">Hủy bỏ</button>
                                </div>
                            </div>
                            </div>
                        </div>
                        </div>
                    </form>
                    </div>
                </div>
                <ToastContainer/>
            </div>
        );
    }
}

export default AddQuestion;