import React, { Component } from 'react';
import axios from 'axios';

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username:"",
            password:"",
            requiredMessage:"",
            validatorUsername:"",
            validatorPassword:""
        }
    }

    isChange = (event)=>{
        const name = event.target.name;
        const value = event.target.value;

        this.setState({
            [name]:value
        });
    }

    onSubmit = (event)=>{
        event.preventDefault();
        if(this.state.username.length === 0){
            this.setState({
                validatorUsername:"Vui lòng nhập tài khoản!",
                requiredMessage:"",
                validatorPassword:""
            });

            return;
        }

        if(this.state.password.length === 0){
            this.setState({
                validatorPassword:"Vui lòng nhập mật khẩu!",
                validatorUsername:"",
                requiredMessage:"",
            });
            return;
        }

        const _user = {
            username: this.state.username,
            password: this.state.password
        }
        
        axios.post('/api/users/authenticate',_user)
        .then((res)=>{
            
            if (res.status === 200) {
                console.log('Login thành công!');
                localStorage.setItem('user',res.data.user.username_id);
                this.setState({
                    requiredMessage:"",
                    validatorUsername:"",
                    validatorPassword:""
                });
                // this.props.hi
                this.props.history.push('/danh-sach-mon-thi');
            }
        })
        .catch((err)=>{
            this.setState({
                requiredMessage:"Tài khoản hoặc mật khẩu không đúng!",
                validatorUsername:"",
                validatorPassword:""
            });
        });
    }
    
    render() {
        return (
            <div className="container-fluid">
                <div className="container">
                    <div className="row" style={{height: 640}}>
                        <div className="col-4 m-auto">
                            <form style={{padding: 20, border: '1px solid #f0f0f0', backgroundColor: '#f0f0f0'}} id="form-login" className="needs-validation">
                            <div className="form-group">
                                <label htmlFor="inputUserName">Tên tài khoản</label>
                                <input type="text" onChange={(event)=>{this.isChange(event)}} className="form-control" name="username" id="inputUserName" aria-describedby="usernameHelp" placeholder="Nhập tài khoản" required/>
                                <div style={{color:'#dc3545', fontSize: '80%'}}>
                                    {this.state.validatorUsername}
                                </div>
                            </div>
                            <div className="form-group">
                                <label htmlFor="inputPassword">Mật khẩu</label>
                                <input type="password" onChange={(event)=>{this.isChange(event)}} name="password" className="form-control" id="inputPassword" placeholder="Nhập mật khẩu" required/>
                                <div style={{color:'#dc3545', fontSize: '80%'}}>
                                    {this.state.validatorPassword}
                                    {this.state.requiredMessage}
                                </div>
                            </div>
                            <button type="submit" onClick={(event)=>{this.onSubmit(event)}} className="btn btn-primary">Đăng nhập</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Login;