import React, { Component } from 'react';
import { Link } from "react-router-dom";
import axios from 'axios';

class Headers extends Component {

    logout = ()=>{
        console.log("đăng xuất");
        
        axios.get('/api/users/logout').then((res)=>{
            if (res.status === 200) {
                window.location.href = '/';
                console.log(res.data.message);
            }
        }).catch((err)=>{
            console.log(err);
        });     
    }
    
    render() {
        return (
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark fixed-top">
                <a className="navbar-brand active" href="true" style={{color: "#ffffff"}}>Thi thử trắc nghiệm</a>
                <button className="navbar-toggler hidden-lg-up" type="button" data-toggle="collapse" data-target="#collapsibleNavId" aria-controls="collapsibleNavId" aria-expanded="false" aria-label="Toggle navigation" />
                <div className="collapse navbar-collapse" id="collapsibleNavId">
                    <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
                        <li className="nav-item">
                            <Link to="/danh-sach-mon-thi" className="nav-link active">
                                Trang Chủ <span className="sr-only">(current)</span>
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/admin/question/add" className="nav-link active">
                                Thêm câu hỏi
                            </Link>
                        </li>
                        <li className="nav-item dropdown" style={{position: 'absolute', right: 15, top: 0}}>
                            <a className="nav-link dropdown-toggle" href="true" id="dropdownId" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <img src={"/images/avatar-default.png"} style={{width: 40, height:40}} alt="Avartar" className="rounded-circle"/>
                            </a>
                            <div className="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownId">
                            <Link to={"/xem-thong-tin." + localStorage.getItem('user')} className="dropdown-item">
                                Thông tin
                            </Link>
                                <span className="dropdown-item" style={{cursor: 'pointer'}} onClick={()=>{this.logout()}}>Đăng xuất</span>
                            </div>
                        </li>
                    </ul>
                </div>
            </nav>

        );
    }
}

export default Headers;